@charset "UTF-8";

/* 기본 요소 초기화 */
body,div,p,dl,dt,
ol,ul,li,
h1,h2,h3,h4,h5,h6,
form,input,select,textarea,button,fieldset,legend,
table,thead,tbody,tfoot,tr,th,td,
header,footer,main,
nav,section,article,aside {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* 텍스트 관련 초기화 */
h1,h2,h3,h4,h5,h6 {
    /* 상속된 폰트 사이즈 사용 */
    font-size: inherit;
    /* 기본 폰트 두께 설정 */
    font-weight: normal;
    
}

/* 테이블 스타일 초기화 */
table {
    /* 테이블 경계 합치기 */
    width: 100%;
    border-collapse: collapse;
}

td,th {
    padding: 0px 0px 0px 0px;
    text-align: left;
    vertical-align: center;
}


/* 앵커 태그 초기화 */
a {
    /* 밑줄 제거 */
    text-decoration: none;
    /* 부모 색상 상속 */    
    color: inherit;

}


ul>li, ol>li {
    list-style-type: none;
}

/* 기본 폼 요소 초기화 */
input, button, textarea, select {
    /* 폰트 패밀리 상속 */
    font-family: inherit;
    /* 폰트 사이즈 상속 */
    font-size: inherit;
    /* 텍스트 색상 상속 */
    color: inherit;
    outline: none;
}

input:focus,
textarea:focus {
    outline: none;
}

/* clearfix */
/* .clearfix{*zoom:1;} */
.clearfix::before, .clearfix::after {
    display: block;
    content: "";
    line-height: 0;
}

.clearfix::after {
    clear: both;
}


/* 숨김처리 */
.blind {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

/* 폰트두께 */
.bold {
    font-weight: bold;
}

/* 폰트정렬 */
.text-right {
    text-align: right !important;
}

.text-left {
    text-align: left !important;
}

.text-center {
    text-align: center !important;
}


