/* 공통(이번에만사용)*********************************************/
/* 버튼 */
.btn {  /* 중간크기 흰색버튼 */
    display: inline-block;
    text-align: center;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
 
    padding: 10px 10px;
    font-size: 14px;
    line-height: 14px;

    color: #000000;
    border: 1px solid #848484;
    background-color: #ffffff;
}

.btn-pri {
    color: #ffffff;
    outline: 1px solid #1B52ED; /* 바깥쪽 경계선 */
    background-color: #1B52ED;
}

.btn-line {
    color: #1B52ED;
    outline: 1px solid #1B52ED; /* 바깥쪽 경계선 */
    background-color: #ffffff;
}


/* 버튼크기 */
.btn-l {  /* 저장등 메인큰 버튼사이즈 */
    width: 100%;
    padding: 12px 0px;
    font-size: 16px;
    line-height: 16px;
}

.btn-m { /* btn만써도됨 중간사이즈 */
    padding: 10px 10px;
    font-size: 14px;
    line-height: 14px;
}

.btn-s { /* 작은 사이즈 */
    padding: 7px 10px;
    font-size: 14px;
    line-height: 14px;
}


