@charset "UTF-8";

/* 메인의 - 레이아웃 **********************************/
main {
    background-color: #fff;
}

main .area-main-header {
    background-color: #ffffff;
}

main .area-main-body {
    background-color: #ffffff;
}

main .area-main-footer {
    background-color: #ffffff;
}

/* 메인바디 레이아웃(.area-main-body)**********************************/
/* 금액 ------------------------------*/
#outItemAdd .box-priceInfo {
    padding: 15px 0px 30px 0px;
    margin-bottom: 15px;
    background-color: #fbfbfb;
    border-bottom: 1px solid #707070;
    text-align: center;
}

#outItemAdd .box-priceInfo .question {
    font-size: 18px;
}

#outItemAdd .box-priceInfo .wrap-price {
    margin-bottom: 0px;
}

#outItemAdd .box-priceInfo .wrap-price .txt-price {
    width: 28px;
    margin-right: 5px;
    font-size: 28px;
    border: none;
    color: red;
    font-weight: bold;
    text-align: right;
}

#outItemAdd .box-priceInfo .wrap-price .unit {
    font-size: 18px;
    color: red;
    margin-right: 5px;
}

#outItemAdd .box-priceInfo .wrap-price .way {
    font-size: 18px;
    margin-bottom: 5px;
}

#outItemAdd .box-priceInfo .basc-price {
    font-size: 16px;
    margin-bottom: 15px;
}

/* 옵션 ------------------------------*/
#outItemAdd .box-priceInfo .box-unit-way {
    background-color: #ebebeb;
    padding: 5px 10px 5px 10px;
    pointer-events: auto;
}

#outItemAdd .box-priceInfo .box-option {
    padding: 5px 0px 5px 0px ;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#outItemAdd .box-priceInfo .box-option .title {
    width: 50px;
    font-size: 16px;
    text-align: left;
}

#outItemAdd .box-priceInfo .box-option .wrap-unit, 
#outItemAdd .box-priceInfo .box-option .wrap-way{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-self: center;
}

#outItemAdd .box-priceInfo .box-option .wrap-unit .btn-unit, 
#outItemAdd .box-priceInfo .box-option .wrap-way .btn-way {
    width: 60px;
    margin-left: 5px
}



/* 사진정보 ----------------------------------------*/
#outItemAdd .box-itemPhoto {
    background-color: #ffffff;
    border-bottom: 1px solid #707070;
    margin-bottom: 15px;
}

/* 지도시간정보 */
#outItemAdd .box-mapTime {
    background-color: #fbfbfb;
    border-bottom: 1px solid #707070;
    margin-bottom: 15px;
}

/* 내역정보 */
#outItemAdd .box-titleMemo {
    background-color: #fbfbfb;
    border-bottom: 1px solid #707070;
    margin-bottom: 15px;
}

/******************************************************/
/* 사진박스 */
/* 사진 타이틀 박스*/
#outItemAdd .box-itemPhoto .wrap-title-btn {
    margin-bottom: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
}

/* 사진 타이틀 */
#outItemAdd .box-itemPhoto .wrap-title-btn .title {
    font-size: 18px;
}

/* 사진 리스트 */
#outItemAdd .box-itemPhoto ul {
    min-height: 90px;
    display: flex;
    margin-bottom: 10px;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 2vw;
}

#outItemAdd .box-itemPhoto ul>li {
    width: 23.3%;
    height: 90px;
    border-radius: 5px;
    border: 1px solid #848484;
    background-color: #dadada;
    overflow: hidden;

    position: relative;
}

#outItemAdd .box-itemPhoto ul>li>img {
    width: 100%;
    height: 100%;
    background-color: #fbfbfb;
    object-fit: cover;
}

#outItemAdd .box-itemPhoto ul>li>.btn-xyt {
    width: 100%;
    padding: 3px;
    border: none;
    background-color: #ffffff;

    font-size: 10px;
    color: #000000;
    text-align: center;

    position: absolute;
    bottom: 0px;
}

#outItemAdd .box-itemPhoto ul>li>.btn-xyt.active {
    background-color: #1B52ED;
    color: #ffffff;
}


#outItemAdd .box-itemPhoto ul>li>.btn-del {
    width: 20px;
    padding: 0px;
    background-color: #000000;
    color: #ffffff;
    position: absolute;
    top: 0px;
    right: 0px
}

/******************************************************/
/* 시간위치 박스 */
/* 시간위치 타이틀*/
#outItemAdd .box-mapTime .wrap-map-time {
    margin-bottom: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
}

#outItemAdd .box-mapTime .wrap-map-time .title {
    font-size: 18px;
}

#outItemAdd .box-mapTime .wrap-map-time .wrap-datetime .btn-datetime {
    margin-left: 5px;
}

#outItemAdd .box-mapTime .map{
    width: 100%;
}


/******************************************************/
/* 항목·내용 박스 */
/* 항목·내용 타이틀*/
#outItemAdd .box-titleMemo .wrap-memo{
    margin-bottom: 3px;
}

#outItemAdd .box-titleMemo .wrap-memo .title {
    font-size: 18px;
}

#outItemAdd .box-titleMemo .content {
    width: 100%;
    height: 70px;;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 18px;
    border: 1px solid #a0a0a0
}




/******************************************************/
/* 키패드 */



.map-container {
    width: 100%;
    height: 200px;
    border-radius: 8px;
}